.home_page_main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 900px;
  max-height: 100vh;

  background: #ecf1f3;
  margin-top: 32px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 50px 32px;

  /* border: 2px solid grey; */
  border-radius: 6px;

  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.home_page_title {
  margin-bottom: 20px;
  font-size: 36px;
  color: #2a2a2a;
}

.home_page_info {
  margin-bottom: 20px;
  font-size: 24px;
  color: #2a2a2a;
}
